.ComponentSkillsAndLocation {
  display: grid;
}
.SkillsDesign {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 10px 0px;
  width: 100%;
}
.SkillsDesign input[type="radio"] {
  display: none;
}
.SkillsDesign input[type="radio"] + label {
  width: 20%;
  text-align: center;
  border: 1px solid white;
  padding: 10px;
}
.SkillsDesign input[type="radio"]:checked + label {
  background-color: #1fb3e5;
  color: white;
}
input[type="checkbox"],input[type="checkbox"]+label{
  margin:0;
}
.SkillAndLocation {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.CheckBox {
  display: grid;
  grid-template-columns: 20px 1fr;
  height:30px;
}
.CheckBox * {
  -ms-flex-item-align: center;
      align-self: center;
}
.navbar{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom:0;
}


